.navbar {
  background: rgba(0, 0, 0, 0.21);

  // background-color: rgb(158, 150, 150);
  display: inline-flex;
  width: 100%;
  position: absolute;
  position: relative;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-size: 15px;
  .logo {
    width: 20%;
    padding: 27px 50px;
    cursor: pointer;
    img {
      width: 160px;
    }
  }
  .options {
    display: flex;
    position: relative;
    width: 60%;
    .option {
      // background-color: rgb(158, 150, 150);
      margin: 30px 10px 0px 10px;
      padding: 10px 14px;
      height: 40px;
      color: white;
      list-style-type: none;
      cursor: pointer;
      &:hover {
        text-shadow: 0.3px 0.3px 1.4px rgba(221, 214, 214, 0.119),
          0.6px 0.8px 3.3px rgba(218, 209, 209, 0.172),
          1.2px 1.5px 6px rgba(255, 239, 239, 0.212),
          1.9px 2.4px 10px rgba(238, 221, 221, 0.248),
          3.2px 4px 16.4px rgba(219, 217, 217, 0.288),
          5.5px 6.9px 28.7px rgba(236, 229, 229, 0.341),
          12px 15px 62px rgba(230, 220, 220, 0.46);
      }
    }
  }
  .btn {
    width: 10%;
    margin: 30px 10px;
    .btn-bookRom {
      border-radius: 30px;
      background: rgb(137, 242, 98);
      background: linear-gradient(
        90deg,
        rgba(137, 242, 98, 1) 3%,
        rgba(74, 199, 27, 1) 43%,
        rgba(39, 217, 19, 0.9780287114845938) 70%,
        rgba(221, 238, 213, 1) 100%
      );
    }
  }
  .menuRight {
    width: 10%;
    .logoMenu {
      width: 40px;
      cursor: pointer;
      margin: 30px 0 0 30%;
      color: white;
      height: 100%;
      .open {
        font-size: 30px;
        padding: 5px;
      }
    }
    .menu {
      z-index: 2;
      background-color: white;
      width: 330px;
      right: 0;
      position: fixed;
      transform: translateX(100%);
      height: 100%;
      font-family: monospace;
      overflow-y: scroll;

      .header {
        margin: auto !important;
        width: 140px;
        color: rgb(117, 114, 114);
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
          Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      }
      .close {
        display: flex;
        p {
          margin-left: 150px;
          margin-top: 30px;
          margin-bottom: 0;
        }
        .btn-close {
          width: 30%;
          cursor: pointer;
          padding: 5px;
          margin-left: 100px;
          .close {
            padding: 5px;
            font-size: 20px;
          }
        }
      }
      .card {
        margin: 20px;
        // background-color: blue;
        .image {
          padding: 10px;
          display: flex;
          width: 100%;
          margin: 5px;
          .inf {
            cursor: pointer;
            .btn-price {
              border-radius: 30px;
              background: rgb(212, 74, 0);
              background: linear-gradient(
                90deg,
                rgba(255, 196, 0, 1) 3%,
                rgba(255, 171, 0, 1) 52%,
                rgba(255, 244, 1, 0.9780287114845938) 100%
              );
              .icon {
                color: red;
                margin-left: 3px;
              }
            }
            .header {
              margin-bottom: 0;
              font-family: "Trebuchet MS", "Lucida Sans Unicode",
                "Lucida Grande", "Lucida Sans", Arial, sans-serif;
            }
            .add {
              display: flex;
              width: 160px;
              margin: 5px;
              h4 {
                margin-top: 0;
                color: rgb(133, 132, 130);
              }
            }
          }
          img {
            width: 40%;
          }
        }
      }
      .menu-hide {
        display: none;
        height: 500px;
        .option {
          width: 100% !important;
          color: wheat;
          padding: 20px;
          cursor: pointer;
          // margin: 5px;
          &:hover {
            background-color: rgb(176, 179, 179);
            color: rgb(170, 169, 169);
            .option1 {
              span {
                text-shadow: 0.3px 0.3px 1.4px rgba(231, 222, 222, 0.119),
                  0.6px 0.8px 3.3px rgba(233, 228, 228, 0.172),
                  1.2px 1.5px 6px rgba(250, 242, 242, 0.212),
                  1.9px 2.4px 10px rgba(238, 221, 221, 0.248),
                  3.2px 4px 16.4px rgba(219, 217, 217, 0.288),
                  5.5px 6.9px 28.7px rgba(236, 229, 229, 0.341),
                  12px 15px 62px rgba(150, 68, 68, 0.46) !important;
              }
            }
          }
          .option1 {
            span {
              color: rgb(247, 241, 241);
              padding: 10px;
              font-size: 18px;
              font-family: "Courier New", Courier, monospace;
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1100px) {
  .logoMenu {
    margin-left: 0px !important;
    .open {
      font-size: 20px;
    }
  }
  .menu{
    background-color: rgb(134, 134, 134) !important;
    color: white;
    .header{
      color: rgb(255, 255, 255) !important;
    }
    .menu-hide{
      display: block !important;
      
    }
    .card{
      display: none;
      
    }
  }
  .options {
    display: none !important;
  }
  .btn {
    display: none;
  }
  .logo {
    margin: auto;
    padding: 27px 0px !important;
    width: 110px !important;
  }
}
